import React from "react";
import { Link } from "gatsby";
import { Link as I18nLink } from "gatsby-plugin-react-i18next";
import styled from "styled-components";
import { colors, device } from "@layouts/styleConfig";
import { MdFrontMatter } from "@tool/types";

type LayoutType = {
  outlineColor: string;
};

type PostType = {
  data: MdFrontMatter;
};

type layoutPropType = {
  [key: string]: string;
};

const Layout = styled.div.withConfig({
  componentId: "Post_Layout",
})<LayoutType>`
  background-color: transparent;

  grid-column: span 4 / span 4;
  margin: 1rem 1.5rem;
  padding-bottom: 8px;
  border-bottom: 1px solid ${colors.lightWhite};

  a {
    text-decoration: none;
    /* color: ${colors.white}; */
    width: 100%;
    transition: 0.3s;

    :hover {
      & .date,
      .post_title {
        color: ${({ theme }) => theme.hoverColor};
      }
    }
  }

  img {
    border-radius: 12px;
    width: 100%;
    height: 150px;
    object-fit: cover;
    position: relative;
    transition: 0.3s;
  }

  .date {
    margin-top: 8px;
    font-size: 16px;
    color: ${({ theme }) => theme.postColor};
    transition: 0.3s;
  }

  .post_title {
    margin-top: 4px;
    font-size: 20px;
    font-weight: 700;
    color: ${({ theme }) => theme.postColor};
    overflow-wrap: break-word;
    transition: 0.3s;
  }

  @media ${device.tablet} {
    padding-bottom: 0;
    border-bottom: none;

    :hover {
      img {
        outline: ${({ outlineColor }) => `2px solid  ${outlineColor}`};
        outline-offset: 2px;
      }
    }

    img {
      height: 200px;
    }

    .date {
      margin-top: 8px;
      font-size: 18px;
    }

    .post_title {
      font-size: 26px;
    }
  }

  @media ${device.laptop} {
    img {
      height: 250px;
    }

    .date {
      margin-top: 8px;
      font-size: 20px;
    }

    .post_title {
      font-size: 30px;
    }
  }
`;

export default function Post({ data }: PostType) {
  const { image_cover, title, path, date, type } = data;

  const outlineColorConfig: layoutPropType = {
    blog: colors.red,
    project: colors.blue,
  };

  return (
    <Layout outlineColor={outlineColorConfig[type] || "white"}>
      <Link to={path}>
        <img alt="post_cover" src={image_cover} className="img_cover" />
        <div className="date">{date.split("T")[0]}</div>
        <div className="post_title">{title}</div>
      </Link>
    </Layout>
  );
}
